export default {
  data() {
    return {
      // 手机号码输入的内容
      telVal: "",
      // 验证码输入的内容
      verificationVal: "",
      // 控制密码的显示与隐藏
      passShow: true,
      passHide: false,
      // 控制手机号码的显示与隐藏
      telShow: true,
      telHide: false
    };
  },
  methods: {
    // 点击手机号码旁边的字 控制显示不同的字样
    passChange() {
      this.passShow = !this.passShow;
      this.passHide = !this.passHide;
    },
    // 点击手机号码旁边的字 控制显示不同的字样
    telChange() {
      this.telShow = !this.telShow;
      this.telHide = !this.telHide;
    },
    // 点击完成去登录页面
    goLogin() {
      uni.navigateTo({
        url: "/pages/login/login"
      });
    }
  },
  computed: {
    isActive() {
      if (this.telVal === "" || this.verificationVal === "") {
        return false;
      }
      return true;
    }
  }
};
